// dropdown style
li.has-dropdown {
    position: relative;

    .submenu {
        min-width: 230px;
        height: auto;
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 90;
        opacity: 0;
        visibility: hidden;
        text-align: left;
        transition: 0.3s;
        border-radius: 0 0 6px 6px;
        background-color: #fff;
        border-left: 0;
        border-bottom: 0;
        border-right: 0;
        display: inline-block;
        box-shadow: 0 36px 35px rgba(0, 0, 0, 0.08);
        padding: 15px 10px;
        transform-origin: 0 0;
        transform: scaleY(0);
        &.new{
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            gap: 0;
            min-width: 450px;
            padding: 30px;
            li{
                flex-basis: 50%;
                a{
                    background: none;
                    padding: 12px !important;
                    border-bottom: 1px solid #f3f3f3 !important;
                    border-radius: 0;
                    color: #071431;
                    font-weight: 600;
                    opacity: 1 !important;
                    font-size: 16px !important;
                    &:hover{
                        background: none;
                        border-bottom: 1px solid var(--color-primary) !important;
                    }
                }
            }
        }

        li {
            margin-right: 0;
            padding: 0;

            a {
                padding: 6px 16px !important;
                font-size: 16px;
                transition: all .3s;
                border-radius: 4px;
                display: block;
                padding: 12px 45px 12px 15px;
                border-radius: 5px;
                background: #FFFFFF;
                transition: all 0.3s;
                text-align: left;
                font-size: 14px;
                font-weight: 600;

                &:hover {
                    // background: var(--color-primary);
                    opacity: 1;
                    background: #F2F2F8;
                    opacity: 1;
                    color: var(--color-primary) !important;
                }
            }
        }
    }

    &:hover {
        .submenu {
            opacity: 1;
            visibility: visible;
            top: 100%;
            transform: translateY(0);
            transform: scaleY(1);
        }

        &::after {
            content: "\f077";
        }
    }
}

.has-dropdown.mega-menu::after {
    display: none;
}

.rts-btn.btn-primary.border.bg-transparent {
    color: var(--color-primary);
    border: 1px solid var(--color-primary) !important;

    img {
        filter: brightness(0) saturate(100%) invert(46%) sepia(55%) saturate(5974%) hue-rotate(228deg) brightness(98%) contrast(103%);
    }

    &:hover {
        background: var(--color-primary) !important;
        color: #fff;

        img {
            filter: brightness(0) saturate(100%) invert(100%) sepia(10%) saturate(7500%) hue-rotate(241deg) brightness(114%) contrast(108%);
        }
    }
}


header .nav-area ul li.main-nav {
    &:hover {
        &>a {
            transition: .3s;
            color: var(--color-primary) !important;
        }

        &::after {
            color: var(--color-primary) !important;
        }
    }
}

.rts-mega-menu {
    position: absolute;
    width: 100%;
    height: auto;
    top: 100%;
    transform: scaleY(0);
    left: 0;
    z-index: 90;
    opacity: 0;
    visibility: hidden;
    text-align: left;
    transition: all 0.5s;
    border-radius: 0 0 5px 5px !important;
    background-color: #ffffff;
    display: inline-block;
    box-shadow: 0 36px 35px rgba(61, 60, 60, 0.08);
    transform-origin: 0 0 0;
    padding: 30px 30px;

    // box-shadow: 0px 25px 60px 0px rgba(0, 0, 0, 0.1);
    &.with-add {
        padding: 0;
        overflow: hidden;
        border-radius: 0 0 10px 10px;

        .menu-add-top-area {
            padding: 30px 0 25px 0;
            border-bottom: 1px solid #E1E1FF;
            margin-left: 50px;

            .title {
                margin-bottom: 0;
                font-size: 24px;
                color: var(--color-primary);
            }
        }

        .menu-right-add {
            display: flex;
            justify-content: flex-end;
            align-items: end;
            max-width: max-content;
            margin-left: auto;
            position: relative;
            border-radius: 0 0 7px 0;
            overflow: hidden;

            .absolute-image {
                img {
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    width: 100%;
                }

                .inner-content {
                    position: absolute;
                    left: 23px;
                    bottom: 23px;

                    .title {
                        color: #fff;
                        font-size: 24px;
                    }

                    .rts-btn {
                        background: #fff;
                        color: var(--color-primary);
                    }
                }
            }
        }

        .mega-menu-item {
            // padding: 15px 30px 40px 25px;

            li a {
                display: flex;
                align-items: center;
                gap: 5px;
                width: 100%;

                img {
                    max-width: 30px;
                    height: auto;
                    padding: 0;
                    background: transparent;
                    transition: .3s;
                    margin-right: 5px;
                }

                &:hover {
                    img {
                        filter: brightness(0) saturate(100%) invert(97%) sepia(3%) saturate(0%) hue-rotate(288deg) brightness(103%) contrast(100%);
                    }
                }

                &.active {
                    img {
                        filter: brightness(0) saturate(100%) invert(97%) sepia(3%) saturate(0%) hue-rotate(288deg) brightness(103%) contrast(100%);
                    }
                }
            }
        }
    }
    &.with-home-demos{
        width: 630px;
        left: unset;
    }
}

.mega-menu-item {
    padding: 0;
    margin: 0;
    flex-direction: column;
    display: flex;
    align-items: flex-start !important;

    li {
        margin-bottom: 19px;
        margin-top: 0;
        margin-right: 0;
        margin-left: 0;
        width: 100%;

        &:hover {
            a {
                border: 1px solid #E6E5FF;
                border-radius: 4px;
            }
        }

        a {
            display: flex !important;
            align-items: center;
            padding: 6px 12px !important;
            border: 1px solid transparent;
            width: 100%;

            img {
                margin-right: 16px;
                padding: 10px;
                max-width: max-content;
                background: #F0F0FF;
                border-radius: 4px;
            }

            .info {
                p {
                    margin-bottom: 2px;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 26px;
                    color: #083A5E;
                }

                span {
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 24px;
                    color: #497696;
                }
            }
        }
    }
}

.has-dropdown.mega-menu {
    position: static !important;
}

.has-dropdown.mega-menu {
    &:hover {
        .rts-mega-menu {
            opacity: 1;
            visibility: visible;
            top: 100%;
            transform: scaleY(1);
        }
    }
}

.container-full-header {
    .rts-mega-menu {
        transform: translateX(-50%) scaleY(0);
        left: 50%;
        max-width: 80%;

        @media #{$laptop-device} {
            max-width: 90%;
        }
    }

    .has-dropdown.mega-menu:hover .rts-mega-menu {
        transform: translateX(-50%) scaleY(1);
    }
}

.has-dropdown.mega-menu {
    &:hover {
        &>a {
            &::after {
                content: "\f077";
                color: var(--color-primary);
            }
        }
    }
}

.appoinment-area-main.contact-page {
    border-radius: 10px;
    background: #F9F8FF;
    border: 1px solid #DDD8F9;
    background-image: none;

    input,
    textarea,
    .custom-select {
        color: var(--color-primary) !important;
        border-color: var(--color-primary) !important;

        span {
            color: var(--color-primary) !important;
        }
    }

    .rts-btn {
        background: var(--color-primary);
        color: #fff;
    }
}

header.header-three .rts-mega-menu.service-mega-menu-style {
    width: 75%;
}

.rts-mega-menu.service-mega-menu-style {
    padding: 30px 30px 30px 30px;
    z-index: 1000;

    li {
        margin-bottom: 20px;
        a {
            border: none;
            width: 100%;
            padding: 18px 25px !important;
            border-radius: 5px;
            border: 1px solid #EAF0FF;
            display: block;
            transition: .3s;

            &.free-consultation {
                background: var(--color-primary);

                .title {
                    color: #fff !important;
                }

                p.details {
                    color: #fff !important;
                }

                &:hover {
                    background: var(--color-primary);
                }
            }

            .single-service-menu {
                display: flex;
                align-items: flex-start;

                .icon {
                    border-radius: 4px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-right: 15px;

                    img {
                        background: none;
                        padding: 0;
                        max-width: 40px;
                        margin: auto;
                    }
                }

                .title {
                    color: var(--color-title);
                    margin-bottom: 11px;
                    line-height: 1.3;
                }

                p.details {
                    max-width: 100%;
                    font-size: 14px;
                    color: var(--color-body);
                }
            }
        }

        &:hover a {
            
        }
    }
    .menu-thumb {
        height: auto;

        img {
            height: auto;
            object-fit: cover;
        }
    }

    .menu-list{
        border-left: 1px solid #EAF0FF;
        padding-left: 35px;
        margin-left: 10px;
        ul{
            flex-direction: column;
            align-items: flex-start;
            gap: 10px;
            li{
                margin-bottom: 0;
            }
        }
    }

}

header .menu-item-open {
    >a {
        color: var(--color-primary) !important;

        &::after {
            content: "\f077" !important;
            color: var(--color-primary) !important;
        }
    }
}

header .menu-item-open.project-a-after>a::after {
    display: none;
}


.menu-item-open {
    &::after {
        color: var(--color-primary) !important;
    }
}

.mega-menu-item.with-list {
    gap: 0;

    li {
        a {
            border: none !important;
            display: flex;
            align-items: center;
            gap: 8px;
            border-bottom: 1px solid #f3f3f3 !important;
            padding: 12px 12px !important;
            transition: .3s;

            i {
                font-size: 12px;
            }

            &.active {
                color: var(--color-primary);
                border: none !important;
                gap: 12px;
                border-radius: 0 !important;
                border-bottom: 1px solid var(--color-primary) !important;
            }
        }

        &:hover {
            a {
                border: none !important;
                gap: 12px;
                border-radius: 0 !important;
                border-bottom: 1px solid var(--color-primary) !important;
            }
        }
    }
}


.sub-dropdown.down {
    position: relative !important;
    display: block !important;

    &::after {
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translateY(-50%);
        content: '\f054';
        font-family: var(--font-3);
    }

    .submenu.third-lvl.base {
        opacity: 0 !important;
        visibility: hidden;
        min-width: 225px !important;
        left: 100% !important;
        top: -25px !important;
        margin: 0;
        border-radius: 0 !important;
        border-radius: 10px !important;
        transition: .3s;
        display: block !important;
    }

    &:hover {
        &::after {
            color: var(--color-primary);
        }

        .submenu.third-lvl.base {
            opacity: 1 !important;
            top: 0 !important;
            right: 0 !important;
            visibility: visible;
            margin-left: 0 !important;
            min-width: 225px !important;
        }
    }
}

li.has-dropdown .submenu li a {
    &.active {
        color: var(--color-primary);
        background: #F2F2F8;
    }
}

.rts-mega-menu.service-mega-menu-style li a {
    &.active {
        background: #fff3f3;
    }
}

.rts-mega-menu.service-mega-menu-style li a {
    &.active.free-consultation {
        background: var(--color-primary);
    }
}

header.header-two .menu-item-open>a {
    color: var(--color-primary) !important;
}

.hega-menu-head-wrapper {
    margin: 0;
    margin-bottom: 10px !important;
    margin-left: 0px !important;
    padding-bottom: 10px !important;
    border-bottom: 1px solid var(--color-primary);

    p {
        font-weight: 700;
        color: var(--color-title);
        font-size: 17px;
        display: flex;
        align-items: center;

        i {
            font-size: 24px;
            margin-right: 8px;
            color: var(--color-primary);
            font-weight: 600;
        }
    }
}

ul.mega-menu-item.parent-nav {
    gap: 0 !important;
}

header.header--sticky.sticky {
    .nav-area ul li.main-nav {
        padding: 0 0;
        cursor: pointer;
    }
}

.header-transparent.header--sticky {
    .nav-area ul li.main-nav:hover>a {
        transition: 0.3s;
        color: #fff !important;
    }
}

.demo-app-development{

    header .nav-area ul li.main-nav:hover > a{
        color: var(--color-white) !important;
        &::after{
            color: var(--color-white) !important;
        }
    } 
    header .nav-area ul li.main-nav:hover::after{
        color: var(--color-white) !important;
    }
}

.demo-app-development,
.demo-machine-learning,
.demo-software-company,
.demo-digital-agency{
    .rts-mega-menu.service-mega-menu-style li a .single-service-menu .icon img{
        filter: brightness(0) saturate(100%) invert(45%) sepia(84%) saturate(4331%) hue-rotate(227deg) brightness(98%) contrast(103%);
    }
}